/* Corpo della pagina */
body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f5;
  color: #333;
}

/* Intestazione */
.header {
  background-color: #2c3e50;
  color: white;
  padding: 1em 0;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.header .logo {
  max-width: 100px;
  margin-bottom: 1em;
}

.header h1 {
  font-size: 2.5em;
  margin: 0;
}

.header p {
  font-size: 1.2em;
  margin: 0.5em 0 0;
}

/* Contenuto principale */
.main-content {
  padding: 2em;
  max-width: 800px;
  margin: auto;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Sezione servizi */
.services h2 {
  font-size: 1.8em;
  margin-bottom: 1em;
}

.services ul {
  list-style-type: none;
  padding: 0;
}

.services li {
  font-size: 1.2em;
  margin: 0.5em 0;
  display: flex;
  align-items: center;
}

.services li::before {
  content: "â€¢";
  color: #2c3e50;
  font-size: 1.5em;
  margin-right: 0.5em;
}

/* Form di prenotazione */
.booking-section {
  margin: 20px;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.booking-section h2 {
  font-size: 1.8em;
  text-align: center;
  margin-bottom: 20px;
}

.booking-form {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 15px 20px;
  align-items: center;
}

.form-row label {
  font-weight: bold;
  font-size: 1em;
  text-align: right;
}

.booking-form input,
.booking-form select {
  padding: 0.8em;
  font-size: 1em;
  border: 1px solid #ccc;
  border-radius: 5px;
  transition: border-color 0.3s ease;
}

.booking-form input:focus,
.booking-form select:focus {
  border-color: #2c3e50;
}

.booking-form button {
  grid-column: 1 / span 2;
  padding: 0.8em;
  font-size: 1.1em;
  background-color: #2c3e50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.booking-form button:hover {
  background-color: #34495e;
  transform: translateY(-2px);
}

/* Footer */
.footer {
  text-align: center;
  padding: 1em 0;
  background-color: #2c3e50;
  color: white;
  margin-top: 2em;
}

.footer p {
  margin: 0;
}

.footer a {
  color: #fff;
  text-decoration: underline;
  margin-top: 10px;
  display: block;
}

/* Responsive per dispositivi mobili */
@media (max-width: 480px) {
  .booking-form {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  .form-row label {
    text-align: left;
  }
}
